import { useState, useEffect, useCallback } from 'react';

/**
 * Hook pro ≈ô√≠zen√≠ light/dark re≈æimu
 * - auto: detekuje syst√©mov√© nastaven√≠
 * - light: vynucen√Ω svƒõtl√Ω re≈æim
 * - dark: vynucen√Ω tmav√Ω re≈æim
 */
export const useThemeMode = () => {
  // Preference: 'auto', 'light', 'dark'
  const [preference, setPreference] = useState(() => {
    if (typeof window !== 'undefined') {
      try {
        const stored = localStorage.getItem('app_theme_preference');
        if (stored) return stored;
      } catch {}
    }
    return 'auto';
  });

  // Aktu√°ln√≠ re≈æim (vypoƒç√≠tan√Ω z preference)
  const [mode, setMode] = useState('light');

  // Detekce syst√©mov√©ho re≈æimu
  useEffect(() => {
    const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
    
    const updateMode = () => {
      if (preference === 'auto') {
        setMode(mediaQuery.matches ? 'dark' : 'light');
      } else {
        setMode(preference);
      }
    };

    updateMode();
    mediaQuery.addEventListener('change', updateMode);
    
    return () => mediaQuery.removeEventListener('change', updateMode);
  }, [preference]);

  // Aplikuj re≈æim na document
  useEffect(() => {
    document.documentElement.setAttribute('data-theme', mode);
  }, [mode]);

  // Ulo≈æen√≠ preference
  useEffect(() => {
    try {
      localStorage.setItem('app_theme_preference', preference);
    } catch {}
  }, [preference]);

  const setThemePreference = useCallback((pref) => {
    setPreference(pref);
  }, []);

  const toggle = useCallback(() => {
    setMode(m => m === 'light' ? 'dark' : 'light');
  }, []);

  return { mode, preference, setThemePreference, toggle };
};

export default useThemeMode;